html {
  overflow-y: scroll;
}

body {
  font-family: Roboto;
  font-size: 16px;
}

h1 {
  text-transform: uppercase;
  font-size: 32px;
  color: #bfb8ad;
}

h2 {
  font-weight: 300;
  font-size: 20px;
  text-transform: uppercase;
  padding-bottom: 5px;
}

a {
  text-decoration: none;
  color: #98002e;
}

a:hover{
  text-decoration: underline;
}

.site {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  height: 100%;
}

.site-content {
  -webkit-box-flex: 1 0 auto;
  -webkit-flex: 1 0 auto;
  -ms-flex: 1 0 auto;
  flex: 1 0 auto;
}

.site-footer {
  color: #fff;
  background: #4c4c4c;
  background: #3c3c3c;
  margin-top: 4em;
}

.site-footer p {
  margin-top: 1em;
}

.page-header {
  margin-top: -15px;
}

.fa.fa-file-word-o {
  color: #2b5797;
}

.fa.fa-file-powerpoint-o {
  color: #d04525;
}

.fa.fa-file-excel-o {
  color: #1e7145;
}

.fa.fa-file-pdf-o {
  color: #c11e07;
}


/*
  Navbar
 */
 .navbar {
   text-transform: uppercase;
   font-size: 14px;
 }

 .navbar form .form-control {
   font-size: 13px;
 }

 .navbar-default .navbar-nav > .active > a,
 .navbar-default .navbar-nav > .active > a:hover,
 .navbar-default .navbar-nav > .active > a:focus {
   background: transparent;
   color: #98002e;
 }

 .navbar-gasps {
   background-color: #fff;
   border-color: #fff;
   border-bottom: 2px solid #98002e;
   height: 100px;
   z-index: 1;
 }

 .navbar-brand-img {
   height: 74px;
 }

 .navbar-collapse {
   margin-top: 26px;
   background-color: #fff;
 }


