.home-icons {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 16px;
  line-height: 30px;
}

.home-icons .fa {
  display: inline-block;
  margin-right: 5px;
  color: #98002e;
  font-size: 30px;
}

.home-icons .well {
  background-color: #bfb8ad;
  border: none;
  white-space: nowrap;
}

.home-icons .well:hover {
  background-color: #b1a99e;
}

.home-icons .well a {
  color: white;
  display: block;
}

.home-bottom {
  border-bottom: 1px solid rgba(191,184,173, .4);
  margin-bottom: 1em;
}

.home-bottom .well {
  background-color: transparent;
  box-shadow: none;
  border: none;
  border-radius: 0;
}

.home-title,
.home-bottom h4 {
  font-weight: 300;
  font-size: 24px;
  text-transform: uppercase;
  padding-bottom: 5px;
}

.home-bottom .home-title {
  font-size: 20px;
  border-bottom: 2px solid #98002e;
}

.home-bottom ul,
.home-bottom ol {
  margin-left: 0;
  padding-left: 1.3em;
}

.home-bottom .list-unstyled {
  padding-left: 0;
}

.home-bottom li {
  padding-bottom: 7px;
}

.home-icons .well a:hover {
  text-decoration: none;
}

.spfgroup {
  padding-bottom: 1em;
  border-bottom: 1px solid rgba(191,184,173, .4);
  margin-bottom: 1em;
}

.img-sponsor {
  display: inline-block;
  padding: 20px;
  height: 78px;
}


/*
Sidebar
 */
.sidebar h5 {
  margin-top: 56px;
  padding-right: 14px;
  text-align: right;
  text-transform: uppercase;
}

.sidebar .nav a {
  text-align: right;
}

.sidebar .nav > li > a:hover,
.sidebar .nav > li > a:focus {
  background-color: transparent;
  text-decoration: underline;
}


/*
Resources Table
 */
.resources-table {
  width: 100%;
}

.resources-table th,
.resources-table td {
  padding: 5px 10px;
}

.resources-table th {
  text-align: left;
  background: #bfb8ad;
  color: #fff;
  font-weight: 400;
}
.resources-table th:active,
.resources-table td:active {
  outline: none;
}

.resources-table .sorting,
.resources-table .sorting_asc,
.resources-table .sorting_desc {
  cursor: pointer;
}
.resources-table .sorting,
.resources-table .sorting_asc,
.resources-table .sorting_desc,
.resources-table .sorting_asc_disabled,
.resources-table .sorting_desc_disabled {
  background-repeat: no-repeat;
  background-position: center right;
}
.resources-table .sorting {
  background-image: url("/assets/images/sort_both.png");
}
.resources-table .sorting_asc {
  background-image: url("/assets/images/sort_asc.png");
}
.resources-table .sorting_desc {
  background-image: url("/assets/images/sort_desc.png");
}
.resources-table .sorting_asc_disabled {
  background-image: url("/assets/images/sort_asc_disabled.png");
}
.resources-table .sorting_desc_disabled {
  background-image: url("/assets/images/sort_desc_disabled.png");
}
.resources-table td {
  vertical-align: top;
}
.resources-table .file-type {
  text-align: center;
  width: 75px;
}
.resources-table .date {
  width: 75px;
}
.resources-table span {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.resources-table .fa {
  font-size: 36px;
}

/*
Training Table
 */
.training-table {
  table-layout: fixed;
  width: 100%;
  margin-bottom: 2em;
}

.training-table th {
  border-bottom: 1px solid red;
  border: 1px solid #ccc;
}

.training-table th,
.training-table td {
  padding: 1em;
  vertical-align: top;
}

.training-table td {
  border: 1px solid #ccc;
}

.training-table-date {
  width: 150px;
}

.training-table-modality {
  width: 160px;
}

.training-table-ceus {
  width: 70px;
  text-align: center;
}

.training-table-archived {
  width: 100px;
  text-align: center;
}

#hellobar.large a.hellobar_logo {
  top: 0 !important;
}
